var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-container"},[_c('el-breadcrumb',[_c('el-breadcrumb-item',{attrs:{"to":{ path: '/' }}},[_vm._v("首頁")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("服務項目")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("美學微整")]),_vm._v(" "),_c('el-breadcrumb-item',[_vm._v("舒顏萃/3D聚左旋乳酸")])],1),_vm._ssrNode(" "),_c('Title',{attrs:{"title":"舒顏萃/3D聚左旋乳酸","titleEn":"SCULPTRA"}}),_vm._ssrNode(" <div class=\"article-container service-page\"><picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/sculptra/banner.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/sculptra/banner.jpg")))+" alt=\"舒顏萃/3D聚左旋乳酸 Banner\"></picture></div> <div class=\"article-container\"><h2>美麗功效</h2> <p><b>舒顏萃 Sculptra®</b> 是坊間所稱的<span class=\"hasColor\">「童顏針」，深獲明星名人喜愛的一種膠原蛋白增生劑，不僅可達到立即填補凹陷撫平皺紋的效果，並且漸進式的刺激膠原蛋白增生增加肌膚的支撐度，改善老化問題。</span>在第13個月可有效矯正不同凹陷程度的鼻唇溝，注射後最長可持續有效校正不同凹陷程度的鼻唇溝達25個月。</p> <p><b>舒顏萃 Sculptra®</b> 是一種聚左乳酸(poly-L-lactic acid)的植入劑，無菌、非熱原性之懸浮液，它是藉由將無菌注射用水加入無菌乾粉調配而成。此懸浮液中含有聚左乳酸(poly-L-lactic acid)的微粒，即聚乳酸(polylactic acid)的結晶型。聚左乳酸(poly-L-lactic acid)具有生物相容性及生物可分解性，它屬於α-羥基酸系列(alpha-hydroxy-acid family)的合成聚合物。</p> <p><b>舒顏萃 Sculptra®可以改善臉部凹陷部位，特別是不同凹陷程度的鼻唇溝、皮膚皺摺及皺紋。</b> 在美國進行的前瞻性、隨機、對照臨床試驗，舒顏萃 Sculptra®已被證明，在第13個月可有效矯正不同凹陷程度的鼻唇溝，注射後最長可持續有效校正不同凹陷程度的鼻唇溝達25個月。</p> <p><b>舒顏萃 Sculptra®</b> 進入組織之後，會被噬菌細胞、纖維母細胞包圍；注射後局部會有膨脹情況，但1~2天水分會被吸收，只剩下藥物繼續刺激膠原蛋白增生。</p></div> <div class=\"article-container\"><h2>舒顏萃 Sculptra®的治療原理</h2> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/sculptra/1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/sculptra/1.png")))+" alt=\"舒顏萃 Sculptra®的治療原理\"></picture> <ul><li>✔ 欲改善深度皺紋，如法令紋、木偶紋眼、唇周細紋等</li> <li>✔ 欲改善臉部凹陷狀態者</li> <li>✔ 希望臉部年輕化，時效更持久、更自然</li></ul></div> <div class=\"article-container\"><h2>舒顏萃 Sculptra®的優點</h2> <ul><li>✔ 在正確的治療下安全性高、副作用少</li> <li>✔ 疼痛感低</li> <li>✔ 術後無恢復期，不影響日常生活</li> <li>✔ 經濟實惠，價格比整形手術低廉許多</li></ul></div> <div class=\"article-container\"><h2>適用對象</h2> <ul><li>+ 希望優雅漸進式的醫學美容(自然呈現)</li> <li>+ 希望改善鼻唇溝(法令紋)</li> <li>+ 希望改善木偶紋 希望有飽滿的夫妻功(太陽穴)</li> <li>+ 希望改善鬆弛雙頰擁有豐盈臉龐</li> <li>+ 希望改善下顎線條</li> <li>+ 在意因歲月而流失的膠原蛋白</li></ul></div> <div class=\"article-container\"><h2>美麗案例</h2> <h4>接受3次注射，總計5瓶療程</h4> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/sculptra/案例1.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/sculptra/案例1.jpg")))+" alt=\"案例1\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h4>接受4次注射，總計6瓶療程</h4> <picture><source"+(_vm._ssrAttr("srcset",require("assets/img/service/beautyService/sculptra/案例2.webp")))+" type=\"image/webp\"> <img"+(_vm._ssrAttr("src",require("assets/img/service/beautyService/sculptra/案例2.jpg")))+" alt=\"案例2\"></picture> <p>療程效果會因個人體質而有所差異</p></div> <div class=\"article-container\"><h2>術前小提醒</h2> <ul><li>+ 切勿使用於慢性皮膚病(感染或發炎)、蟹足腫(keloids)患者禁止使用。</li> <li>+ 治療範圍應盡量避免太陽或紫外線燈的曝曬。</li> <li>+ 注射後會有些微出血、疼痛、在注射點出現局部紅腫現象、瘀血、輕微腫脹，皆為正常，3~7天左右可復原。</li> <li>+ 注射後因填充材質而呈現水腫，一般而言3~7天逐漸消退。</li> <li>+ 水腫現象解除後，最初缺陷會暫時重現，在接下來的數週術後，3D聚左旋乳酸(PLLA)將刺激患者自體膠原生成來填補流失的凹陷處。</li> <li>+ 少數敏感肌因注射物質而產生敏感性丘疹及結節，治療前須依醫師指示評估膚況。</li></ul></div> <div class=\"article-container\"><h2>術後小叮嚀</h2> <ul><li>+ 注射治療後，需立即進行深層的按摩，以幫助3D聚左旋乳酸均勻分散。</li> <li>+ 按摩後，使用冰敷袋，在治療部位冰敷，以減少術後的水腫。</li> <li>+ 居家照護，按摩5.5.5。術後5天內，建議患者每天自行於治療部位進行5次、每次5分鐘的按摩，除了以按摩促使3D聚左旋乳酸均勻 分佈，加速膠原蛋白的增生作用，使治療效果更自然、更理想。</li></ul> <h4>本療程的效果可能因個人體質的不同而有差異，請務必與醫師溝通諮詢</h4></div>")],2)}
